<template>
  <div>
    <h2>ActiveSelection</h2>
    <p>继承自 fabric.Group</p>
    <h3>成员</h3>
    <h3>corner :number|string|any</h3>
    <p>在鼠标移动期间保留最后一个悬停角的值。0表示无角，或“mt”、“ml”、“mtr”等。。它应该是私有的，但将其用作只读属性没有坏处。</p>
    <h3>类型</h3>
    <p>number | string | any</p>
    <p>默认值：0</p>
    <h3>absolutePositioned :boolean</h3>
    <p>只有当对象用作clipPath时才有意义。如果为true，clipPath将具有相对于画布的顶部和左侧，并且不受对象变换的影响。这将使clipPath相对于画布，但仅剪切特定对象。警告：这是测试版，此功能可能会更改或重命名。
    </p>
    <p>默认值：false</p>
    <h3>aCoords</h3>
    <p>描述对象在画布中的角点位置对象的绝对坐标属性有tl、tr、bl、br和描述四个主角点。每个属性都是一个对象，具有织物点. 坐标取决于以下属性：width、height、scaleX、scaleY
      skewX、skewY、angle、strokeWidth、top、left。这些坐标对于理解物体的位置很有用。它们会用ocords更新，但在缩放或平移更改时不需要更新。用@method
      setCoords更新坐标。您可以不使用@method calcACoords（）更新而计算它们
    </p>
    <h3>angle:Number</h3>
    <p>旋转角度</p>
    <h3>backgroundColor :String</h3>
    <p>对象的背景色</p>
    <h3>borderColor :String</h3>
    <p>对象激活时，它的控制器边框颜色</p>
    <h3>borderDashArray :Array</h3>
    <p>指定对象边框虚线图案的数组</p>
    <h3>borderOpacityWhenMoving :Number</h3>
    <p>Opacity of object's controlling borders when object is active and moving</p>

    <h3>

      borderScaleFactor :Number
    </h3>
    <p>

      Scale factor of object's controlling borders bigger number will make a thicker border border is 1, so this is
      basically a border thickness since there is no way to change the border itself.
    </p>
    <h3>

      cacheProperties :Array
    </h3>
    <p>

      Groups are container, do not render anything on theyr own, ence no cache properties
    </p>
    <h3>

      centeredRotation :Boolean
    </h3>
    <p>

      When true, this object will use center point as the origin of transformation when being rotated via the controls.
      Backwards incompatibility note: This property replaces "centerTransform" (Boolean).
    </p>
    <h3>

      centeredScaling :Boolean
    </h3>
    <p>

      When true, this object will use center point as the origin of transformation when being scaled via the controls.
      Backwards incompatibility note: This property replaces "centerTransform" (Boolean).
    </p>

    <h3>clipPath :fabric.Object</h3>

    <p>
      a fabricObject that, without stroke define a clipping area with their shape. filled in black the clipPath object
      gets used when the object has rendered, and the context is placed in the center of the object cacheCanvas. If you
      want 0,0 of a clipPath to align with an object center, use clipPath.originX/Y to 'center'
    </p>
    <h3>colorProperties :Array</h3>
    <p>
      List of properties to consider for animating colors.
    </p>
    <h3> cornerColor :String</h3>
    <p> Color of controlling corners of an object (when it's active)
    </p>
    <h3> cornerDashArray :Array</h3>
    <p> Array specifying dash pattern of an object's control (hasBorder must be true)
    </p>
    <h3> cornerSize :Number</h3>
    <p> Size of object's controlling corners (in pixels)
    </p>
    <h3> cornerStrokeColor :String</h3>
    <p> Color of controlling corners of an object (when it's active and transparentCorners false)
    </p>
    <h3>cornerStyle :String</h3>
    <p> Specify style of control, 'rect' or 'circle'
    </p>
    <h3> dirty :Boolean</h3>
    <p> When set to `true`, object's cache will be rerendered next render call. since 1.7.0
    </p>
    <h3>evented :Boolean</h3>
    <p> When set to `false`, an object can not be a target of events. All events propagate through it. Introduced in
      v1.3.4
    </p>
    <h3>excludeFromExport :Boolean</h3>
    <p>When `true`, object is not exported in OBJECT/JSON
    </p>
    <h3> fill :String</h3>
    <p> Color of object's fill takes css colors https://www.w3.org/TR/css-color-3/
    </p>
    <h3>fillRule :String</h3>
    <p>Fill rule used to fill an object accepted values are nonzero, evenodd Backwards incompatibility note: This
      property
      was used for setting globalCompositeOperation until v1.4.12 (use `fabric.Object#globalCompositeOperation` instead)
    </p>
    <h3>flipX :Boolean</h3>
    <p>When true, an object is rendered as flipped horizontally
    </p>
    <h3> flipY :Boolean</h3>
    <p>When true, an object is rendered as flipped vertically
    </p>
    <h3>globalCompositeOperation :String</h3>
    <p>Composite rule used for canvas globalCompositeOperation
    </p>
    <h3> hasBorders :Boolean</h3>
    <p> When set to `false`, object's controlling borders are not rendered
    </p>
    <h3>hasControls :Boolean</h3>
    <p> When set to `false`, object's controls are not displayed and can not be used to manipulate object
    </p>
    <h3> height :Number</h3>
    <p>Object height
    </p>
    <h3>hoverCursor :String</h3>
    <p>Default cursor value used when hovering over this object on canvas
    </p>
    <h3>includeDefaultValues :Boolean</h3>
    <p> When `false`, default object's values are not included in its serialization
    </p>
    <h3> inverted :boolean</h3>
    <p>Meaningful ONLY when the object is used as clipPath. if true, the clipPath will make the object clip to the
      outside
      of the clipPath since 2.4.0
    </p>
    <h3>left :Number</h3>
    <p>Left position of an object. Note that by default it's relative to object left. You can change this by setting
      originX={left/center/right}
    </p>
    <h3> lockMovementX :Boolean</h3>
    <p> When `true`, object horizontal movement is locked
    </p>
    <h3> lockMovementY :Boolean</h3>
    <p>When `true`, object vertical movement is locked
    </p>
    <h3> lockRotation :Boolean</h3>
    <p> When `true`, object rotation is locked
    </p>
    <h3> lockScalingFlip :Boolean</h3>
    <p> When `true`, object cannot be flipped by scaling into negative values
    </p>
    <h3> lockScalingX :Boolean</h3>
    <p>When `true`, object horizontal scaling is locked
    </p>
    <h3> lockScalingY :Boolean</h3>
    <p>When `true`, object vertical scaling is locked
    </p>
    <h3> lockSkewingX :Boolean</h3>
    <p> When `true`, object horizontal skewing is locked
    </p>
    <h3> lockSkewingY :Boolean</h3>
    <p> When `true`, object vertical skewing is locked
    </p>
    <h3> minScaleLimit :Number</h3>
    <p> Minimum allowed scale value of an object
    </p>
    <h3> moveCursor :String</h3>
    <p> Default cursor value used when moving this object on canvas
    </p>
    <h3> noScaleCache :Boolean</h3>
    <p> When `true`, cache does not get updated during scaling. The picture will get blocky if scaled too much and will
      be
      redrawn with correct details at the end of scaling. this setting is performance and application dependant. default
      to true since 1.7.0
    </p>
    <h3> objectCaching :Boolean</h3>
    <p> When `true`, object is cached on an additional canvas. When `false`, object is not cached unless necessary (
      clipPath ) default to true
    </p>
    <h3> oCoords</h3>
    <p>Describe object's corner position in canvas element coordinates. properties are depending on control keys and
      padding the main controls. each property is an object with x, y and corner. The `corner` property contains in a
      similar manner the 4 points of the interactive area of the corner. The coordinates depends from the controls
      positionHandler and are used to draw and locate controls
    </p>
    <h3> opacity :Number</h3>
    <p> Opacity of an object
    </p>
    <h3> originX :String</h3>
    <p> Horizontal origin of transformation of an object (one of "left", "right", "center") See
      http://jsfiddle.net/1ow02gea/244/ on how originX/originY affect objects in groups
    </p>
    <h3> originY :String</h3>
    <p> Vertical origin of transformation of an object (one of "top", "bottom", "center") See
      http://jsfiddle.net/1ow02gea/244/ on how originX/originY affect objects in groups
    </p>
    <h3> ownMatrixCache</h3>
    <p> storage for object transform matrix
    </p>
    <h3> padding :Number</h3>
    <p>Padding between object and its controlling borders (in pixels)
    </p>
    <h3> paintFirst :String</h3>
    <p> Determines if the fill or the stroke is drawn first (one of "fill" or "stroke")
    </p>
    <h3> perPixelTargetFind :Boolean</h3>
    <p> When set to `true`, objects are "found" on canvas on per-pixel basis rather than according to bounding box
    </p>
    <h3> scaleX :Number</h3>
    <p> Object scale factor (horizontal)
    </p>
    <h3>scaleY :Number</h3>
    <p> Object scale factor (vertical)

      When set to `false`, an object can not be selected for modification (using either point-click-based or group-based
      selection). But events still fire on it.
    </p>
    <h3> selectionBackgroundColor :String</h3>
    <p>Selection Background color of an object. colored layer behind the object when it is active. does not mix good
      with
      globalCompositeOperation methods.
    </p>
    <h3>shadow :fabric.Shadow</h3>
    <p> Shadow object representing shadow of this shape
    </p>
    <h3> skewX :Number</h3>
    <p> Angle of skew on x axes of an object (in degrees)
    </p>
    <h3> skewY :Number</h3>
    <p>Angle of skew on y axes of an object (in degrees)
    </p>
    <h3>statefullCache :Boolean</h3>
    <p>When `true`, object properties are checked for cache invalidation. In some particular situation you may want this
      to
      be disabled ( spray brush, very big, groups) or if your application does not allow you to modify properties for
      groups child you want to disable it for groups. default to false since 1.7.0
    </p>
    <h3> stateProperties :Array</h3>
    <p>List of properties to consider when checking if state of an object is changed (fabric.Object#hasStateChanged) as
      well as for history (undo/redo) purposes
    </p>
    <h3> stroke :String</h3>
    <p> When defined, an object is rendered via stroke and this property specifies its color takes css colors
      https://www.w3.org/TR/css-color-3/
    </p>
    <h3> strokeDashArray :Array</h3>
    <p>Array specifying dash pattern of an object's stroke (stroke must be defined)
    </p>
    <h3> strokeDashOffset :Number</h3>
    <p> Line offset of an object's stroke
    </p>
    <h3> strokeLineCap :String</h3>
    <p> Line endings style of an object's stroke (one of "butt", "round", "square")
    </p>
    <h3> strokeLineJoin :String</h3>
    <p>Corner style of an object's stroke (one of "bevel", "round", "miter")
    </p>
    <h3> strokeMiterLimit :Number</h3>
    <p> Maximum miter length (used for strokeLineJoin = "miter") of an object's stroke
    </p>
    <h3> strokeUniform :Boolean</h3>
    <p>When `false`, the stoke width will scale with the object. When `true`, the stroke will always match the exact
      pixel
      size entered for stroke width. default to false
    </p>
    <h3> strokeWidth :Number</h3>
    <p> Width of stroke
    </p>
    <h3> subTargetCheck :Boolean</h3>
    <p> Indicates if click, mouseover, mouseout events & hoverCursor should also check for subtargets
    </p>
    <h3> top :Number</h3>
    <p> Top position of an object. Note that by default it's relative to object top. You can change this by setting
      originY={top/center/bottom}
    </p>
    <h3> touchCornerSize :Number</h3>
    <p>Size of object's controlling corners when touch interaction is detected
    </p>
    <h3> transparentCorners :Boolean</h3>
    <p>When true, object's controlling corners are rendered as transparent inside (i.e. stroke instead of fill)
    </p>
    <h3> useSetOnGroup :Boolean</h3>
    <p> setOnGroup is a method used for TextBox that is no more used since 2.0.0 The behavior is still available setting
      this boolean to true.
    </p>
    <h3> visible :Boolean</h3>
    <p> When set to `false`, an object is not rendered on canvas
    </p>
    <h3> width :Number</h3>
    <p> Object width</p>
