import Vue from "vue";
import VueRouter from "vue-router";

// 引入组件
import ActiveSelection from "../views/ActiveSelection.vue";
import BaseBrush from "../views/BaseBrush.vue";
import Canvas from "../views/Canvas.vue";
import Circle from "../views/Circle.vue";
import CircleBrush from "../views/CircleBrush.vue";
import Color from "../views/Color.vue";
import Ellipse from "../views/Ellipse.vue";
import Gradient from "../views/Gradient.vue";
import Group from "../views/Group.vue";
import Image from "../views/Image.vue";
import BaseFilter from "../views/BaseFilter.vue";
import Blur from "../views/Blur.vue";
import Brightness from "../views/Brightness.vue";
import ColorMatrix from "../views/ColorMatrix.vue";
import Contrast from "../views/Contrast.vue";
import Convolute from "../views/Convolute.vue";
import BlendColor from "../views/BlendColor.vue";
import BlendImage from "../views/BlendImage.vue";

// 要告诉 vue 使用 vueRouter
Vue.use(VueRouter);

const routes = [
  {
    path: '/',
    redirect: '/ActiveSelection'
  },
  {
    path: "/ActiveSelection",
    component: ActiveSelection
  },
  {
    path: "/BaseBrush",
    component: BaseBrush
  },
  {
    path: "/Canvas",
    component: Canvas
  },
  {
    path: "/Circle",
    component: Circle
  }
  ,
  {
    path: "/CircleBrush",
    component: CircleBrush
  },
  {
    path: "/Color",
    component: Color
  },
  {
    path: "/Ellipse",
    component: Ellipse
  },
  {
    path: "/Gradient",
    component: Gradient
  },
  {
    path: "/Group",
    component: Group
  },
  {
    path: "/Image",
    component: Image
  },
  {
    path: "/BaseFilter",
    component: BaseFilter
  },
  {
    path: "/Blur",
    component: Blur
  },
  {
    path: "/Brightness",
    component: Brightness
  },
  {
    path: "/ColorMatrix",
    component: ColorMatrix
  },
  {
    path: "/Contrast",
    component: Contrast
  },
  {
    path: "/Convolute",
    component: Convolute
  },
  {
    path: "/BlendColor",
    component: BlendColor
  },
  {
    path: "/BlendImage",
    component: BlendImage
  }
]

var router = new VueRouter({
  routes
})
export default router;